<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>编辑商品</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图区域 -->
    <el-card>
      <!-- 提示区域 -->
      <el-alert title="编辑商品信息:  商品参数、商品属性和商品图片都已恢复默认值" type="warning" show-icon center :closable="false">
      </el-alert>

      <!-- 步骤条区域 -->
      <el-steps :space="200" :active="activeIndex - 0" finish-status="success" align-center>
        <el-step title="基本信息"></el-step>
        <el-step title="商品参数"></el-step>
        <el-step title="商品属性"></el-step>
        <el-step title="商品图片"></el-step>
        <el-step title="商品内容"></el-step>
        <el-step title="完成"></el-step>
      </el-steps>

      <!-- tab栏区域 -->
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-position="top" status-icon>
        <el-tabs :tab-position="'left'" v-model="activeIndex" :before-leave="beforeTabLeave" @tab-click="tabClicked">
          <el-tab-pane label="基本信息" name="0">
            <el-form-item label="商品名称" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="商品价格" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品重量" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品数量" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="商品分类" prop="goods_cat">
              <el-cascader v-model="addForm.goods_cat" :options="catelist" :props="cateProps" @change="handleChange"></el-cascader>
            </el-form-item>
          </el-tab-pane>

          <el-tab-pane label="商品参数" name="1">
            <!-- 渲染表单的item项 -->
            <el-form-item :label="item.attr_name" v-for="item in manyTableData" :key="item.attr_id">
              <!-- 渲染复选框 -->
              <el-checkbox-group v-model="item.attr_vals">
                <el-checkbox :label="item1" v-for="(item1,i) in item.attr_vals" :key="i" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品属性" name="2">
            <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <el-tab-pane label="商品图片" name="3">
            <!--action 必选参数，上传的地址 -->
            <el-upload :action="uploadURL" :on-preview="handlePreview" :on-remove="handleRemove" list-type="picture" :headers="headersObj" :on-success="handleSuccess">
              <el-button size="small" type="primary">点击上传</el-button>
              <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label="商品内容" name="4">
            <!-- 富文本编辑器组件 -->
            <quill-editor ref="myQuillEditor" v-model="goods_introduce" />
            <el-button type="primary" class="btnAdd" @click="editGoods">修改商品</el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
    <!-- 图片预览对话框 -->
    <el-dialog title="图片预览" :visible.sync="previewVisible" width="30%">
      <img :src="previewPath" class="previewImg">
    </el-dialog>
  </div>

</template>

<script>
import _ from 'lodash'
export default {
    data () {
        return {
            // 步骤条进行状态
            activeIndex: '0',
            // 修改表单
            editForm: {},
            // 添加表单的数据对象
            addForm: {
                goods_name: '',
                goods_price: '',
                goods_weight: '',
                goods_number: '',
                goods_cat: [],
                pics: [],
                goods_introduce: '',
                attrs: []
            },
            // 分类列表数据
            catelist: [],
            // 商品动态参数数据
            manyTableData: [],
            // 商品静态属性数据
            onlyTableData: [],
            // 上传的图片临时路径（对象）
            picsData: [],
            // 添加表单的验证规则
            addFormRules: {
                goods_name: [
                    { required: true, message: '请输入商品名称', trigger: 'blur' }
                ],
                goods_price: [
                    { required: true, message: '请输入商品价格', trigger: 'blur' }
                ],
                goods_weight: [
                    { required: true, message: '请输入商品重量', trigger: 'blur' }
                ],
                goods_number: [
                    { required: true, message: '请输入商品数量', trigger: 'blur' }
                ],
                goods_cat: [
                    { required: true, message: '请选择商品分类', trigger: 'blur' }
                ]
            },
            // 级联选择框的参数
            cateProps: {
                expandTrigger: 'hover',
                label: 'cat_name',
                value: 'cat_id',
                children: 'children'
            },
            // 图片上传的URL地址
            uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
            // 图片上传请求头
            headersObj: {
                Authorization: window.sessionStorage.getItem('token')
            },
            // 预览的图片路径
            previewPath: '',
            // 显示 / 隐藏 图片预览对话框
            previewVisible: false,
            // 商品详情描述信息
            goods_introduce: ''
        }
    },
    created () {
        this.getGoodInfoById()
        // 获取所有商品分类数据
        this.getCateList()
    },
    methods: {
    // 根据id获取商品信息
        async getGoodInfoById () {
            const id = location.href.split('#')[1].split('/')[3]
            //  获取编辑商品的id
            const res = await this.$http.get(`goods/${id}`)
            if (res.meta.status !== 200) {
                return this.$message.error('商品信息获取失败!')
            }
            this.editForm = res.data
            this.addForm.goods_name = this.editForm.goods_name
            this.addForm.goods_price = this.editForm.goods_price
            this.addForm.goods_weight = this.editForm.goods_weight
            this.addForm.goods_number = this.editForm.goods_number
            const goods_cat = []
            this.editForm.goods_cat.split(',').forEach(item => {
                item = item - 0
                goods_cat.push(item)
            })
            this.addForm.goods_cat = goods_cat
            this.goods_introduce = this.editForm.goods_introduce
        },
        // 获取所有商品分类数据
        async getCateList () {
            // 向服务器发送请求 获取商品分类列表数据
            const res = await this.$http.get('categories')
            // 如果获取失败 服务器返回的状态码不为200
            if (res.meta.status !== 200) {
                // 返回并提示错误信息
                return this.$message.error('获取商品分类列表数据失败!')
            }
            // 将获取的商品分类数据保存到data中
            this.catelist = res.data
        },
        // 级联选择框选项变化时 触发函数
        handleChange () {
            // 如果用户选择的不是三级商品分类
            if (this.addForm.goods_cat.length !== 3) {
                // 清空级联选择框内容
                this.addForm.goods_cat = []
                // 提示用户错误信息并返回
                return this.$message.error('请选择三级商品分类')
            }
        },
        beforeTabLeave (activeName, oldActiveName) {
            // 判断addForm是否填了5个数据
            let count = 0
            for (const k in this.addForm) {
                if (this.addForm[k]) {
                    count++
                }
            }
            // 如果用户选择的不是三级商品分类且未填满五个数据
            if (this.addForm.goods_cat.length !== 3 && count < 5) {
                // 清空级联选择框内容
                this.addForm.goods_cat = []
                // 提示用户错误信息
                this.$message.error('请完善商品基本信息!')
                return false
            }

            // 可以从高完成项返回低完成项
            if (oldActiveName - activeName > 0) {
                return true
            }
            // 如果第一步已经完成 可以选择第二步
            if (oldActiveName === '0' && activeName === '1') {
                return true
            }
            // 如果第二步已经完成 可以选择第三步
            if (oldActiveName === '1' && activeName === '2') {
                return true
            }
            // 如果第三步已经完成 可以选择第四步
            if (oldActiveName === '2' && activeName === '3') {
                return true
            }
            // 如果第四步已经完成 可以选择第五步
            if (oldActiveName === '3' && activeName === '4') {
                return true
            }
            return false
        },
        // tab 被选中时触发
        async tabClicked () {
            // 如果点击了动态参数面板
            if (this.activeIndex === '1') {
                const res = await this.$http.get(`categories/${this.cateId}/attributes`, { params: { sel: 'many' } })
                if (res.meta.status !== 200) {
                    return this.$message.error('获取商品参数失败!')
                }
                res.data.forEach(item => {
                    item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
                })
                this.manyTableData = res.data
            } else if (this.activeIndex === '2') {
                const res = await this.$http.get(`categories/${this.cateId}/attributes`, { params: { sel: 'only' } })
                if (res.meta.status !== 200) {
                    return this.$message.error('获取商品属性失败!')
                }
                this.onlyTableData = res.data
            }
        },
        // 处理图片预览效果
        handlePreview (file) {
            this.previewPath = file.response.data.url
            this.previewVisible = true
        },
        // 移除图片时 调用函数
        handleRemove (file) {
            // 1.获取图片的临时路径
            const filePath = file.response.data.tmp_path
            // 2.从picsData数组中找到图片的索引值
            const index = this.picsData.findIndex(item => item.pic === filePath)
            // 3.利用索引值调用数组的splice删除对应的图片
            this.picsData.splice(index, 1)
        },
        // 监听图片上传成功的事件
        handleSuccess (response, file, fileList) {
            // 1.拼接得到一个图片信息对象
            const picInfo = { pic: response.data.tmp_path }
            // 2.将图片对象push到picsData数组中
            this.picsData.push(picInfo)
        },
        // 编辑商品
        editGoods () {
            // 进行表单校验
            this.$refs.addFormRef.validate(async valid => {
                // 如果校验失败 提示错误信息 并返回
                if (!valid) {
                    return this.$message.error('请填写必要表单项!')
                }
                // 执行添加的业务逻辑
                // 进行深拷贝
                const form = _.cloneDeep(this.addForm)
                form.goods_cat = form.goods_cat.join(',')
                // 处理动态参数和静态属性
                // 处理动态数组
                this.manyTableData.forEach(item => {
                    const newInfo = {
                        attr_id: item.attr_id,
                        attr_value: item.attr_vals.join(',')
                    }
                    form.attrs.push(newInfo)
                })
                // 处理静态属性
                this.onlyTableData.forEach(item => {
                    const newInfo = {
                        attr_id: item.attr_id,
                        attr_value: item.attr_vals
                    }
                    form.attrs.push(newInfo)
                })
                // 赋值图片路径数据
                form.pics = this.picsData
                // 赋值商品详情描述数据
                form.goods_introduce = this.goods_introduce
                // 向服务器发送请求 修改商品
                const res = await this.$http.put(`goods/${this.editForm.goods_id}`, form)
                // 如果请求失败 状态码不为200
                if (res.meta.status !== 200) {
                    // 提示失败信息并返回
                    return this.$message.error('修改商品失败!')
                }
                // 提示成功信息
                this.$message.success('修改商品成功!')
                // 跳转到商品列表页面
                this.$router.push('/goods')
            })
        }
    },
    computed: {
    // 获取三级商品分类id
        cateId () {
            if (this.addForm.goods_cat.length === 3) {
                return this.addForm.goods_cat[2]
            }
            return null
        }
    }
}
</script>

<style lang="less" scoped>
.el-checkbox {
  margin-right: 5px !important;
}
.previewImg {
  width: 100%;
}
.btnAdd {
  margin-top: 15px;
}
</style>